.App {
    display: flex;
}
.App > * {
    background-color: var(--txt-color);
}

@media (max-width: 1000px) {
    .App {
        justify-content: center;
        flex-direction: column;
    }
    .App > * {
        font-size: 3vw;
        position: static;
        width: 100%;
        padding: 0;
        margin: 0;
    }
}